<template>
  <div class="detail-page">
    <van-nav-bar
      left-text="返回"
      @click-left="$router.back()"
      fixed
      title="面经详情"
    />
    <header class="header">
      <h1>{{ article.stem }}</h1>
      <p>
        {{ article.createdAt }} | {{ article.views }} 浏览量 | {{ article.like }} 点赞数
      </p>
      <p>
        <img :src="article.avatar" alt="" />
        <span>作者:{{ article.createor }}</span>
      </p>
    </header>
    <main class="body" v-html="article.content">

    </main>
    <div class="opt">
      <van-icon @click="lToggle" :class="{active:article.likeFlag}" name="like-o"/>
      <van-icon @click="cToggle" :class="{active:article.collectFlag}" name="star-o"/>
    </div>
  </div>
</template>

<script>
import { getArticlesDetail, updateCollect, updateLike } from '@/api/article'
export default {
  name: 'detail-page',
  data () {
    return {
      article: {}
    }
  },
  async created () {
    // console.log(this.$route.params.id)
    const res = await getArticlesDetail(this.$route.params.id)
    // 在网络查看对象
    this.article = res.data
  },
  methods: {
    async cToggle () {
      await updateCollect(this.article.id)
      // 收藏 collectFlag 1 非收藏 0
      this.article.collectFlag = !this.article.collectFlag
      // console.log(this.article)
      if (this.article.collectFlag) {
        this.$toast.success('收藏成功')
      } else {
        this.$toast.success('取消收藏')
      }
    },
    async lToggle () {
      await updateLike(this.article.id)
      this.article.likeFlag = !this.article.likeFlag
      if (this.article.likeFlag) {
        this.$toast.success('点赞成功')
      } else {
        this.$toast.success('点赞收藏')
      }
    }
  }
}

</script>

<style lang="less" scoped>
.detail-page {
  margin-top: 44px;
  overflow: hidden;
  padding: 0 15px;
  .header {
    h1 {
      font-size: 24px;
    }
    p {
      color: #999;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .opt {
    position: fixed;
    bottom: 100px;
    right: 0;
    > .van-icon {
      margin-right: 20px;
      background: #fff;
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      border-radius: 50%;
      box-shadow: 2px 2px 10px #ccc;
      font-size: 18px;
      &.active {
        background: #FEC635;
        color: #fff;
      }
    }
  }
}
</style>
